:root {
  --blue: #007bff;
  --indigo: #6610f2;
  --purple: #6f42c1;
  --pink: #e83e8c;
  --red: #dc3545;
  --orange: #fd7e14;
  --yellow: #ffc107;
  --green: #28a745;
  --teal: #20c997;
  --cyan: #17a2b8;
  --white: #fff;
  --gray: #6c757d;
  --gray-dark: #343a40;
  --primary: #007bff;
  --secondary: #6c757d;
  --success: #28a745;
  --info: #17a2b8;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #ddd;
  --dark: #343a40;
}

#root {
  height: 100%;
}

body, html {
  height: 100%;
}

.app {
  min-height:100%;
  position:relative;
}

main {
  padding-bottom: 12rem;
}

footer {
  position:absolute;
  bottom:0;
  width:100%;
  height:12rem;
}
